import React from "react";
import Package from "./Package";

export default function Packages() {
  const packages = [
    {
      num: 1,
      title: "System Understanding",
      text: "Gain a deep understanding of your system's architecture, processes, and potential entry points. Ensure that our strategies are tailored to your specific environment, setting the stage for a comprehensive security assessment.",
    },
    {
      num: 2,
      title: "Vulnerability Assessment",
      text: "Employ state-of-the-art tools to conduct thorough vulnerability assessments, identifying weaknesses in your infrastructure. Uncover potential risks that could be exploited by attackers, allowing us to prioritize mitigation efforts effectively.",
    },
    {
      num: 3,
      title: "Penetration Testing",
      text: "Perform targeted penetration testing to simulate real-world attacks based on insights from our vulnerability assessment. Test your defenses under controlled conditions, ensuring that your security measures hold strong.",
    },
    {
      num: 4,
      title: "Consulting",
      text: "Provide tailored advice on fortifying your security posture. Implement advanced security solutions and align with industry standards to ensure your organization is well-prepared to face any cyber threat.",
    },
  ];

  return (
    <div className="packages">
      <h2>Our Approach</h2>
      <div className="package-items">
        {packages.map((pack, i) => (
          <Package pack={pack} key={i} />
        ))}
      </div>
    </div>
  );
}
