import React, {useRef, useEffect} from "react";
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);

export default function Package({ pack }) {
  const cardRef = useRef(null);
  useEffect(() => {
    const element = cardRef.current;
    gsap.fromTo(element, {
      x: '-100%',
      opacity: 0,
    },{
      x: '0%',
      duration: 1,
      opacity: 1,
      scrollTrigger: {
        trigger: '.packages',
        start: "top 60%",
        end: "top 30%",
      },
    });
  }, []);
  return (
    <div ref={cardRef} className={`package flex flex-col justify-around self-stretch`} style={{backgroundColor: "var(--white-smoke)"}}>
      {/* <p style={{ fontWeight: "bolder" }}>Package {pack.num}</p> */}
      <h4>{pack.title}</h4>
      <p>{pack.text}</p>
    </div>
  );
}
